// transition






@mixin transition($time: .3s, $delay: 0) {
	@include prefix(transition, $time all ease $delay);
}
@mixin transition2($arg) {
	@include prefix(transition, $arg);
}


@mixin transition-delay($delay) {
	@include prefix(transition-delay, $delay);
}
























